.card {
    margin-bottom: 25px;
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    word-wrap: break-word;
    border-radius: 4px;
    border: 1px solid rgba(0, 0, 0, 0.1);
}

    .card:last-child {
        margin-bottom: 0;
    }

    .card-body {
        padding: 20px;
    }

    .card-title {
        font-size: 20px;
        line-height: 24px;
        font-weight: 500;
        margin-bottom: 20px;
        display: block;
    }

    .card p {
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 20px;
    }

    .card-iframe {
        position: relative;
        height: 0;
        padding-bottom: 56.25%;
    }

    .card-iframe iframe {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
        }

    .card-link {
        display: block;
        padding: 16px 12px;
        border: 1px solid rgba(0, 0, 0, 0.1);
        margin-bottom: 20px;
    }

    .card-tags {
        margin-top: 10px;
    }

    .card-tags li {
            margin-top: 10px;
            margin-right: 10px;
            display: inline-block;
            vertical-align: top;
        }

    .card-tags li:last-child {
                margin-right: 0;
            }

    .card-tags a {
            color: rgba(0, 0, 0, 0.65);
        }
