.card-photo .card-photos {
        margin-bottom: 20px;
    }
    .card-photo.photoset .card-photos {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-row-gap: 20px;
            grid-column-gap: 20px;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
        }
    .card-photo.photoset .card-photos li {
                display: none;
            }
    .card-photo.photoset .card-photos li:nth-child(3n + 1) {
                grid-column: 1/3;
            }
    .card-photo.photoset .card-photos li:nth-child(-n + 3) {
                display: block;
            }
    .card-photo .card-picture {
        display: block;
        width: 100%;
        height: auto;
    }
